.Card-Container{
    min-width: 290px;
    max-width: 320px;
    min-height: 190px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    padding: .7em;
    background-color: white;
    color: black;
    border-radius: 1em;
    margin-top: 1em;
    margin-bottom: 1em;
    margin-left:  0.5em;
    margin-right: 0.5em;
    text-align: center;
    text-justify: center;
    display: grid;
    grid-template-areas: 
    " img title title"
    " img   p     p  "
    " img   p     p  "
    " img  price price "
    " img  chart chart"   
    ;
}
.img-container{
    grid-area: img;
    width: 134px;
    align-self: center;
    margin-right: 1em;
}
.product-img{
    width: 100%;
    height: 134px;
    object-fit: contain;
}
.card-title{
    grid-area: title;
    -webkit-margin-after: 4px;
            margin-block-end: 4px;
    height: auto;
}
.price{
    grid-area: price;
    -webkit-margin-before: 0;
            margin-block-start: 0;
    -webkit-margin-after: 0;
            margin-block-end: 0;
    font-size: 20px;
    font-weight: 600;
}
.description{
    grid-area: p;
    -webkit-margin-before: 0;
            margin-block-start: 0;
    -webkit-margin-after: 0;
            margin-block-end: 0;
}
.chart-btn{
    grid-area: chart;
    outline: 0;
    padding: 7px;
    color: white;
    background-color: #000;
    text-align: center;
    cursor: pointer;
    width: 100%;
    height: 40px;
    font-size: 12px;
    font-weight: 600;
}
.chart-btn:active {
    -webkit-transform: translateY(-2px);
            transform: translateY(-2px);
}
.seemore{
    grid-area: sm;
    border: none;
    box-shadow: 2px 4px 8px 2px rgba(0, 0, 0, 0.2);
    width: 3em;
    height: 2em;
}
.carousel-container{
    width: 95%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0.5em;   
}
.carousel-title{
    font-family: Agency FB;
    font-size: 24 px;
    -webkit-margin-after: 0;
            margin-block-end: 0;
    font-weight: 200;   
}
.cards-container{
    width: 100%;
    height: auto;
    display: flex;
    overflow-x: auto;
    overflow-y: visible;
    scroll-behavior: smooth;
    position: relative;
}
.cards-container::-webkit-scrollbar{
    display: none;
}
.carousel-buttons{
    position: absolute;
    margin-top: 1em;
    width: 100%;
    text-align: center;
    display: flex;
    justify-content: space-between;
}

.btn-carousel-cards{
    background-color: transparent;
    border: none;
    cursor: pointer;
    margin-left: 5px;
    margin-right: 5px;
    border-radius: 50%;
    height: 50px;
    width: 30px;
    position: relative;
    z-index: 20;
}
.btn-carousel-cards::after{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: white;
    top: 0px;
    left: 0px;
    content: "";
    border-radius: 50%;
    -webkit-filter: blur(6px);
            filter: blur(6px);
    z-index: -1;
}
.Category-card{
    position: relative;
